body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
}

.App {
  background: #4c2a4c;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
}

.navbar {
  padding: 10px 20px;
  text-align: center;
  background: #6d3d6d;
}

.navbar h1 {
  margin: 10px 0;
}

.book-list {
  margin: 20px;
  padding: 0;
}

.book-list li {
  background: #6d3d6d;
  list-style-type: none;
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
}

.book-list li:hover {
  opacity: 0.7;
  text-decoration: line-through;
}

.book-list .title {
  font-weight: bold;
  color: #fff;
  font-size: 1.2em;
}

.book-list .author {
  font: size 0.9em;
  color: #ddd;
}

.empty {
  margin: 20px;
  text-align: center;
}

.form {
  padding: 20px;
}

.form-control {
  width: 100%;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  border: 0;
  color: #fff;
  display: block;
  height: 50px;
  padding-bottom: 30px;
}

.form-submit {
  margin: 10px auto;
  background: #eee;
  border: 0;
  padding: 5px 20px;
  border-radius: 5px;
  display: block;
}
